import React from 'react';
import './bootstrap.css'
import './App.css';



class App extends React.Component {
  state = {
    data: null,
    count: 0,
    questionElement: {
      question: '',
      answers: [],
      rightAnswer: ''
    },
    wrong: 0,
    right: 0,
    hide: true,
    btnClass: "qst-btn",
    questionClass: 'questions-hide',
    alert: "alert-hide",
    alertBool: true,
    isManagingFocus: false,
    endAlert: "alert-hide",
    rightAnswerAlert: '',
    qstElement: 'container'
  }

  componentDidMount() {
    fetch('https://opentdb.com/api.php?amount=10&category=11&difficulty=easy&type=multiple')
      .then(res => {
        return res.json();
      })
      .then(data => {
        this.setState({
          data: data,
          hide: false
        })
      })
  }

  getOne = () => {
    const func = function (o) {
      for (var j, x, i = o.length; i; j = parseInt(Math.random() * i), x = o[--i], o[i] = o[j], o[j] = x);
      return o;
    };
    let count = this.state.count
    const data = this.state.data.results
    if (count <= data.length - 1) {
      const question = this.convertHTML(data[count].question)
      const rightAnswer = this.convertHTML(data[count].correct_answer)
      const incorrectAnswer = data[count].incorrect_answers.map(answer => this.convertHTML(answer))
      const answers = func([rightAnswer, ...incorrectAnswer])


      this.setState({
        count: count + 1,
        btnClass: 'btn btn-primary btn-hide',
        questionClass: 'questions',
        questionElement: {
          answers: answers,
          question: question,
          rightAnswer: rightAnswer
        }
      })
    } else {
      this.setState({
        endAlert: "alert alert-dismissible alert-primary btn-end",
        qstElement: "btn-hide"
      })
    }

  }

  convertHTML = str => {
    const entities = {
      "&amp;": "&",
      "&lt;": "<",
      "&gt;": ">",
      "&quot;": '"',
      "apos;": "'",
      '&#039;': "'"
    }
    return str.replace(/&#?\w+;/g, match => entities[match])
  }

  setScore = e => {
    e.preventDefault()
    if (e.target.innerText === this.state.questionElement.rightAnswer) {
      this.setState({
        right: this.state.right + 1,
        alert: 'alert alert-dismissible alert-success',
        alertBool: false,
      })
    } else {
      this.setState({
        wrong: this.state.wrong + 1,
        alert: 'alert alert-dismissible alert-danger',
        alertBool: true,
        rightAnswerAlert: this.state.questionElement.rightAnswer
      })
    }
    setTimeout(() => {
      this.setState({ alert: 'alert-hide' })
    }, 3000);
    this.getOne()
  }

  closeAlertWindow = () => {
    this.setState({ alert: 'alert-hide' })
    this.getOne()
  }

  restartGame = () => {
    window.location.reload()
  }



  render() {
    return (
      <div className="App">
        <h1 className="bg-primary">Movie Quiz</h1>
        <h5 className="score bg-dark">SCORE: {this.state.right} / 10</h5>
        <div className={this.state.endAlert}>
          {this.state.right <= 5 ? `Your score is: ${this.state.right}. Better luck next time.` : `GREAT JOB! Your score is: ${this.state.right}. Keep up the good work!`}
          <br />
          <button onClick={this.restartGame} className="again-btn">PLAY AGAIN</button>
        </div>
        <div className={this.state.alert}>
          <button type="button" className="close" onClick={this.closeAlertWindow} data-dismiss="alert">&times;</button>
          {this.state.alertBool ? `The right answer was: ${this.state.rightAnswerAlert}` : "AWESOME YOU'RE DOING GREAT!"}
        </div>
        <div className={this.state.qstElement}>
          <h3 id="question">{this.state.questionElement.question}</h3>
          <div className={this.state.questionClass}>
            <button type="button" onClick={this.setScore} className="btn-lg btn-block btn-qst" href="3">{this.state.questionElement.answers[0]}</button>
            <button type="button" onClick={this.setScore} className="btn-lg btn-block btn-qst" href="3">{this.state.questionElement.answers[1]}</button>
            <button type="button" onClick={this.setScore} className="btn-lg btn-block btn-qst" href="3">{this.state.questionElement.answers[2]}</button>
            <button type="button" onClick={this.setScore} className="btn-lg btn-block btn-qst" href="3">{this.state.questionElement.answers[3]}</button>
          </div>
        </div>
        <button className={this.state.btnClass} type="button" onClick={this.getOne}>GET QUESTIONS</button>
      </div>
    );
  }
}

export default App;
